.inputParent{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background:  linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    /* background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%); */
    
}
.title{
    /* border:linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%) 2px solid; */
    color: #ffffff;
    font-size: 70px;
    /* box-shadow: 0 2px 1px 0 rgba(0,0,0,0.2); */
}
.card
{
    background: #ffffff;
    padding: 20px;
    border-radius: 15px;
    border: #dfdfdf solid 1px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;    
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.input{
    width: 100%;
    text-align: end;
    padding: 10px;
    margin-top: 10px;
}
.inputContainer{
    
    margin-top: 20px;
    margin-bottom: 20px;
}
.submitbtn{
    border-radius: 15px;
    width: 50%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(34,193,195);
background: linear-gradient(280deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
}
.submitbtn:hover{
    background: rgb(2,0,36);
background: linear-gradient(280deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: aliceblue;
}

.popup{
    padding: 20px;
    background-color: #33ccff;
    color: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.poptitle{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;

}
.popbtn{
    width: 100%;
    background: white;
    border-radius: 5px;
    border: none;
    height: 25px;
}
.popbtn:hover{
    background: rgb(34,193,195);
    background: linear-gradient(280deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%); 
}

.colordes{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
 
}
ul{
    display: flex;
    flex-direction: column;
    list-style: none;
    align-items: flex-start;
    margin-right: 10px;
}
.result{
    display: flex;
    flex-direction: row;
}